$white : #e2f3f5;
$cyan : #22d1ee;
$purple : #3d5af1;
$darkBlue : #0e153a;

@font-face {
    font-family: 'Nunito-ExtraLight';
    src: url('./fonts/Nunito/Extra-Light/Nunito-ExtraLight.eot'); /* IE9 Compat Modes */
    src: url('./fonts/Nunito/Extra-Light/Nunito-ExtraLight.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('./fonts/Nunito/Extra-Light/Nunito-ExtraLight.woff') format('woff'), /* Modern Browsers */
         url('./fonts/Nunito/Extra-Light/Nunito-ExtraLight.ttf')  format('truetype'), /* Safari, Android, iOS */
         url('./fonts/Nunito/Extra-Light/Nunito-ExtraLight.svg#svgNunito-ExtraLight') format('svg'); /* Legacy iOS */
}
