$lightWhite : #ebebeb;
$white : #e2f3f5;
$cyan : #22d1ee;
$purple : #3d5af1;
$darkBlue : #0e153a;

.competence{
    font-family: "Nunito-SemiBold";
    font-size: 1.617rem;
    text-align: center;
    border-top: .1rem solid $cyan;
    border-bottom: .1rem solid $cyan;
    width: 100%;
    min-height: 35rem;
    margin-bottom: 2rem;
    padding-top: 1rem;
}
.competence p{
    margin: 1.5rem 0 1.5rem 0;
}
.competence__title{
    font-family: "Nunito-Bold";
    color: $purple;
    font-size: 2rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
}
.competence__description{
    font-family: "Nunito-Regular";
    margin-bottom: 1rem;
    font-size: 1.25rem;
}
.competence__sub-description{
    font-family: "Nunito-ExtraLight";
}
@media (min-width: 801px) {
    .competence{
        padding: 2vh;
        border: .3vh solid $cyan;
        border-radius: 15px;
        width: 45vh;
        min-height: 80vh;
    }
    .competence__title{
        font-size: 4vh;
        padding-top: 2vh;
        padding-bottom: 2vh;
    }
    .competence__description{
        padding-top: 2vh;
        border-top: .3vh solid $cyan;
        margin: 4vh 0 2vh 0;
        font-size: 2.5vh;
    }
    .competence__description p{
        margin: 3vh 0 3vh 0;
    }
}